// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  computed: {

  }
})

//onload function
function addLoadEvent(func) {
	var oldonload = window.onload;
	if (typeof window.onload != 'function') {
		window.onload = func;
	} else {
		window.onload = function() {
			if (oldonload) {
				oldonload();
			}
			func();
		}
	}
}

//Value for listening button demarrer;
demarrerPressed = false;
//Animate Background image to renew each 3s.
var demarrerPressed = false;
function animateBg(){
  var i = 6;
  var name = "Fond";
  document.getElementById('body').style.animationName = "bgAnimate";
  var countStart = setInterval(function(){
      if (demarrerPressed == true) {
        name = "Fond-Flou";
        i = 6;
        demarrerPressed = false;
      }
      i--;
      document.getElementById('body').style.backgroundImage = "url(" + require('./assets/' + name + i + '.jpg'); + ")";
      if (i == 1) {
        i = 7;
      }
  }, 3000);
};

addLoadEvent(animateBg);
addLoadEvent(animateTxt);

//Animate text on radar to change every 1.5s;
function animateTxt(){
    var arrTxt = ['toujours économique',
                'toujours en avance',
                'toujours sécurisé',
                'toujours à jour',
                'toujours disponible',
                'toujours fonctionnel'
              ];
    var k = document.getElementById('text');
    var i = 1;
    k.style.animationName = "customFade";
    var txtCountStart = setInterval(function(){
          k.innerHTML = arrTxt[i];
          if (i == 5) {
            i = -1;
          }
          if (!document.getElementById('bienvenue').style.display == 'none'){
            clearInterval(txtCountStart);
          }
          i++;
    }, 1500);
}

//Animate button then introduction page will load first loop;
var animateButton = (function(){
    var animateButtonStart = setInterval(function(){
        var btn = document.getElementById('radarBtn');
        btn.style.border = '4px solid #00b4dd';
        btn.style.color = '#00b4dd';
        btn.disabled = false;
        btn.style.transform = 'scale(1.1)';
    }, 3000);
})();

//Change #Bienvenue to #Slider with animation.
function goSlider(){
    document.getElementById('body').style.backgroundImage = "url(" + require('./assets/Fond-Flou6.jpg'); + ")";
    document.getElementById('radarBtn').style.opacity = 0;
    document.getElementById('bienvenue').classList.add("fadeOut");
    demarrerPressed = true;
    setInterval(function(){
      document.getElementById('radarBtn').style.display = "none";
      document.getElementById('bienvenue').style.display = "none";
      document.getElementById('slider').style.display = "block";
      document.getElementById('header').style.display = "flex";
    }, 500);
}
document.getElementById('radarBtn').addEventListener("click", goSlider);

$('.carousel').carousel({
  interval: 0
})



