<template>
  <div class="main" id="app">
    <Introduction></Introduction>
  </div>
</template>

<script>
import Introduction from './components/Introduction'

export default {
  name: 'App',
  components: {
    Introduction,
  }
}
</script>

<style>
*, *:before, *:after{
  box-sizing: border-box;
}
html, body{
  margin: 0;
  width: 100%;
  height: 100%;
}
.main{
  height: 100%;
  width: 100%;
}
</style>



